body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.p-staff-blog__header {
  text-align: center;
}

.p-staff-blog__subtitle {
  margin-top: -0.1875rem;
}

.p-staff-blog-block {
  position: relative;
}

.p-staff-blog-block::before {
  position: absolute;
  content: "";
  background-color: rgba(175, 158, 151, 0.6);
  width: 100vw;
  height: 100%;
  margin-left: calc(50% - 50vw);
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  top: 0;
  left: 0;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .p-staff-blog-block::before {
    height: 31.1875rem;
    top: 234px;
  }
}

.p-staff-blog-card__img {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.25);
}

.p-staff-blog__textblock {
  padding-top: 0.625rem;
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .p-staff-blog__textblock {
    padding-top: initial;
    margin-top: 1.75rem;
  }
}

.p-staff-blog__text {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .p-staff-blog__text-sp {
    display: none;
  }
}

.p-staff-blog__card {
  margin: 1.25rem auto 0 auto;
}
@media screen and (min-width: 768px) {
  .p-staff-blog__card {
    max-width: 62.5rem;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 1.8125rem auto 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .p-staff-blog-card__block:nth-child(n+4) {
    margin-top: 2.5rem;
  }
}

@media screen and (min-width: 768px) {
  .p-staff-blog-card__block {
    width: 18.5rem;
  }
}

.p-staff-blog__button-block {
  padding-top: 2.3125rem;
  padding-bottom: 2.5rem;
  text-align: center;
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .p-staff-blog__button-block {
    padding-bottom: initial;
  }
}

.p-staff-blog__button {
  background-color: #F7F7F7;
  color: #4D4D4D;
  border: 2px solid #4D4D4D;
  margin: 0.125rem 0 -0.0625rem 0;
  transition: 0.3s;
  padding: 0.96875rem 5.08125rem;
}
@media screen and (min-width: 768px) {
  .p-staff-blog__button {
    padding: 1rem 6.1875rem 0.875rem 6.1875rem;
  }
}

.p-staff-blog__button:hover {
  transform: translateY(-5px);
  background-color: #4D4D4D;
  color: #F7F7F7;
}